import { useState, useEffect, useRef, useContext } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { SocketContext } from '@/context/SocketContext';
import { addMessage,fetchChatHistory} from '@/redux/Slices/MessageSlice';
import { markMessagesAsRead } from '@/redux/Slices/MessageSlice';
export default function ChatBox({ receiverId, receiverType, currentUserId, currentUserType }) {
  const socket = useContext(SocketContext);
  const dispatch = useDispatch();
  const { messages,conversationId ,loading, error } = useSelector((state) => state.message);
  const [input, setInput] = useState('');
  const messagesEndRef = useRef(null);

useEffect(() => {
  if (!conversationId) return;

    if (messages.some(msg => !msg.read && msg.receiverId === currentUserId)) {
      dispatch(markMessagesAsRead({ conversationId, userId: currentUserId }));
    }
  }, [messages, conversationId, currentUserId, dispatch]);

  useEffect(() => {
    dispatch(fetchChatHistory({participant1:currentUserId, participant2:receiverId }));
  }, [dispatch, currentUserId, receiverId]);

  useEffect(() => {
    if (!socket) return;

    const handleNewMessage = (msg) => {
      if (
        (msg.senderId === receiverId && msg.receiverId === currentUserId) ||
        (msg.senderId === currentUserId && msg.receiverId === receiverId)
      ) {
        dispatch(addMessage(msg));
      }
    };

    socket.on('newMessage', handleNewMessage);

    return () => {
      socket.off('newMessage', handleNewMessage);
    };
  }, [socket, receiverId, currentUserId, dispatch]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const sendMessage = () => {
    if (input.trim() === '') return;

    socket.emit('sendMessage', {
      senderId: currentUserId,
      receiverId,
      senderType: currentUserType,
      receiverType,
      message: input.trim(),
    });

    setInput('');
  };

  const chatMessages = messages.filter(
    (msg) =>
      (msg.senderId === receiverId && msg.receiverId === currentUserId) ||
      (msg.senderId === currentUserId && msg.receiverId === receiverId)
  );





  return (
    <div className="border p-4 rounded shadow">
      <div className="h-64 overflow-y-auto bg-gray-100 p-2 mb-2">
        {loading && <div className="text-center text-gray-500">Loading chat history...</div>}
        {error && <div className="text-center text-red-500">Error: {error}</div>}
        {chatMessages.map((msg) => (
          <div
            key={msg._id}
            className={`mb-1 ${msg.senderId === currentUserId ? 'text-right' : 'text-left'}`}
          >
            <span className="inline-block bg-white p-1 rounded shadow">{msg.message}</span>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>
     
      <div className="flex">
        <input
          className="flex-1 border p-1 rounded-l"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
        />
        <button className="bg-blue-500 text-white px-4 rounded-r" onClick={sendMessage}>
          Send
        </button>
      </div>
    </div>
  );
}